function capitalizeList (array) {
    const capitalizedArray = []
    for (var i = 0; i < array.length; i++){
        const firstLetter = array[i].charAt(0).toUpperCase()
        const restOfWord = array[i].substring(1)
        const combined = firstLetter + restOfWord
        capitalizedArray.push(combined)
    }

    return capitalizedArray
}

function importAll(r) {
    let images = {};
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
    return images;
}

export { capitalizeList, importAll }