
import React from "react";
import Box from "@material-ui/core/Box"
import Button from "@material-ui/core/Button";
import MobileStepper from "@material-ui/core/MobileStepper";
import Paper from "@material-ui/core/Paper";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import Typography from "@material-ui/core/Typography";
import { useTheme } from "@material-ui/core/styles";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import { capitalizeList, importAll } from '../../utils'

const images = importAll(require.context('../../images/movies', false, /\.(png|jpe?g)$/));

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const Collection = []

for (const image in images) {
    const label = capitalizeList(image.slice(0, -4).split('_')).join(" ")
    Collection.push({
        label: label,
        imgPath: images[image]
    })
}

const MoviesLibrary = () => {
    const CollectionSize = Collection.length;
    const theme = useTheme();
    const [activeStep, setActiveStep] = React.useState(0);
   
    const handleNext = () => {
      setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
      setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };
    
    const handleChange = (step) => {
        setActiveStep(step);
      };

    return (
        <div>
            <Box sx={{maxWidth: 255, flexGrow: 1}}>
                <Paper
                    square
                    elevation={0}
                    sx={{
                        display: "flex",
                        alignItems: "center",
                        height: 50,
                        pl: 2,
                        bgcolor: 'background.default'
                    }}
                >
                    <Typography> {Collection[activeStep].label} </Typography>
                </Paper>
                <AutoPlaySwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={activeStep}
                    onChangeIndex={handleChange}
                    enableMouseEvents
                >
                    {Collection.map((step, index) => (
                    <div key={step.label}>
                        {Math.abs(activeStep - index) <= 2 ? (
                        <Box
                            component="img"
                            sx={{
                            height: 255,
                            width: 255,
                            display: 'block',
                            overflow: 'hidden',
                            alignContent: 'center'
                            }}
                            src={step.imgPath}
                            alt={step.label}
                        />
                        ) : null}
                    </div>
                    ))}
                </AutoPlaySwipeableViews>
                <MobileStepper
                    variant="text"
                    steps={CollectionSize}
                    position="static"
                    activeStep={activeStep}
                    nextButton={
                        <Button
                        size="small"
                        onClick={handleNext}
                        disabled={activeStep === CollectionSize - 1}
                        >
                        Next
                        {theme.direction === 'rtl' ? (
                            <KeyboardArrowLeft />
                        ) : (
                            <KeyboardArrowRight />
                        )}
                        </Button>
                    }
                    backButton={
                        <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
                        {theme.direction === 'rtl' ? (
                            <KeyboardArrowRight />
                        ) : (
                            <KeyboardArrowLeft />
                        )}
                        Back
                        </Button>
                    }
                    />
            </Box>
        </div>
        
    );
  };
    
export default MoviesLibrary